aside ul {
  list-style-type: none;
  text-transform: uppercase;
}

aside ul li {
  line-height: 5vh;
}

aside ul a {
  display: block;
  padding-left: 1vw;
  font-size: 1.8vh;
  font-weight: 700;
  color: #000;
  text-decoration: none;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

aside ul a:hover {
  margin-left: 1vw;
}

aside ul a.active {
  margin-left: 0;
  border-left: .5vw solid yellow;
}

aside ul li:first-child a.active {
  border-color: green;
}

aside ul li:nth-child(2) a.active {
  border-color: #00f;
}

aside ul li:nth-of-type(3) a.active {
  border-color: rgba(255, 0, 0, 1);
}

aside ul li:nth-of-type(4) a.active {
  border-color: rebeccapurple;
}
.homePage>article{
  padding: 2vh 0;
  color: #111;
  border-bottom: .1vw solid #ccc;
}
.homePage>article:first-of-type{
  padding-top: 0;
}
.homePage h1{
  font-size: 2rem;
}

.homePage h2{
  margin: .6vh 0;
  font-size: 1.2rem;
  font-style: italic;
}

.homePage p{
  font-size: 1rem;
}
.productsPage h1::first-letter{
  text-transform: uppercase;
}
.productsPage ul{
  margin-left:2vw;
  font-size: 1.4rem;
}

.productsPage li{
  margin-top: 1vh;
  line-height: 2rem;
}

.productsPage li:hover{
  list-style-type: circle;
}

.productsPage a{
  display: inline-block;
  color: #000;
  text-decoration: none;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

.productsPage a:hover{
  padding-left: .5vw;
  color: #666;
  letter-spacing: .15vw;
}
.productPage h2{
  margin: 2vh 0;
}

.productPage a{
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

.productPage a:hover{
  letter-spacing: .1vw;
}
.contactPage {
  height: 100%;
}

.contactPage h1::first-letter,
.contactPage h2::first-letter {
  text-transform: uppercase;
}

.contactPage h2.errorMessage {
  color: rgba(255, 0, 0, 1)
}

.contactPage form {
  margin-top: 1.5vh;
  width: 80%;
  height: 70%;
}

.contactPage form textarea {
  width: 100%;
  height: 70%;
}

.contactPage form button {
  display: block;
  margin: 1vh 0 0 auto;
  padding: 1.2vh 1.5vw;
  font-weight: 700;
  background-color: beige;
  border: .1vw solid #000;
  cursor: pointer;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

.contactPage form button:hover {
  color: #fff;
  background-color: #000;
  border-color: beige;
}
.adminPage h2 {
  margin: 5vh 0;
}

.adminPage button {
  padding: 1.2vh 1.5vw;
  font-weight: 700;
  background-color: beige;
  border: .1vw solid #000;
  cursor: pointer;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

.adminPage button:hover {
  color: #fff;
  background-color: #000;
  border-color: beige;
}
.loginPage h1::first-letter,.loginPage h2::first-letter{
  text-transform: uppercase;
}
.loginPage form{
  padding: 2vh 0;
}

.loginPage form span,.loginPage form button{
  margin-left: 1vw;
}

.loginPage form button{
  padding: .35vh .5vw;
  font-weight: 700;
  background-color: beige;
  border: .1vw solid #000;
  cursor: pointer;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

.loginPage form button:hover{
  color: #fff;
  background-color: #000;
  border-color: beige;
}

.loginPage h2{
  margin-top: 1vh;
  font-size: 1.2rem;
}

.loginPage h2.errorMessage{
  color: #f00;
}
.errorPage{
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.errorPage h1{
  letter-spacing: .3vw;
}
.footer h2::first-letter, .footer h3::first-letter, .footer h3:nth-of-type(2){
  text-transform: uppercase;
}
.footer h2{
  padding: .5vh;
  font-size: 1.2rem;
}
.footer h3{
  padding: .1vh;
  font-size: .8rem;
}
.footer h3:nth-of-type(2) span{
  text-transform: lowercase;
}
.footer span{
  color: rgba(255,0,0,1)
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

div#root{
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 100vh;
  margin: 0 auto;
}

#root>header{
  position: relative;
  flex-basis: 30%;
  overflow: hidden;
}
#root>header>img{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  min-width: 100%;
  min-height: 100%;
}
#root>main{
  display: flex;
  flex-grow: 1;
}
#root>main>aside{
  flex-basis: 20%;
  padding: 2vh; 
}

#root>main>div.pageContent{
  flex-grow: 1;
  max-width: 80%;
  padding: 2vh; 
}

#root>footer{
  flex-basis: 10%;
  color: #fff;
  background-color: #000;
  text-align: center;
}
